.showcaseGroup {
    display: flex;
    margin-top: calc(50vh - (100vh - 200px) / 2);
    z-index: 2;

    &>* {
        margin-right: 100px;

        &:last-child {
            margin-right: 0;
        }
    }

    @include media(0, $screen-xl) {
        flex-direction: column;
        align-items: center;
        height: fit-content;

        &>* {
            margin: 0;
            margin-bottom: 100px;
        }
    }
}

.showcase {
    position: relative;
    display: flex;
    justify-content: center;
    background-color: $color-dark-lighter;
    z-index: 1;
    width: fit-content;
    height: fit-content;
    max-width: 100vw;
    box-shadow: $shadow;

    &__image {
        width: calc(100vh - 200px);
        height: calc(100vh - 200px);

        object-fit: cover;
    }

    &__index {
        position: absolute;
        top: 20px;
        right: 20px;
        color: $color-primary;
        font-weight: bold;
    }

    &__infoContainer {
        box-sizing: border-box;
        height: calc(100vh - 200px - 120px - 60px);
        margin: 60px 60px;
        width: 400px;
    }

    &__info {
        display: flex;
        flex-direction: column;
        max-height: 100%;
        overflow-y: auto;
    }

    &__name {
        font-size: $font-size-large;
        margin-top: 0;
        margin-bottom: 60px;
    }

    &__date {
        align-self: center;
        margin-top: 10px;
        color: $color-medium;
    }

    &__general {
        font-size: $font-size-normal;
        margin-top: 0;
    }

    &__actions {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 20px;
    }

    &__links {
        height: 40px;
    }

    &__link {
        display: inline-flex;
        width: 40px;
        height: 100%;
        font-size: 40px;
        margin-right: 20px;

        &:last-child {
            margin-right: 0;
        }
    }

    &__moreInfo {
        display: flex;
        align-items: center;
        height: 40px;
        padding: 20px;
    }

    @include media(0, $screen-xl) {
        flex-direction: column;
        align-items: center;
        height: fit-content;

        &__image {
            width: calc(100vw - 200px);
            height: calc(100vw - 200px);

            object-fit: cover;
        }

        &__index {
            top: calc(100vw - 200px + 20px);
            right: 20px;
            color: $color-primary;
            font-weight: bold;
        }

        &__infoContainer {
            box-sizing: border-box;
            // height: calc(100vw - 200px - 120px - 60px);
            height: fit-content;
            margin: 0;
            margin-bottom: 100px;
            margin-top: 60px;
            width: calc(100% - 100px);
        }

        &__name {
            margin-bottom: 20px;
        }

        &__date {
            align-self: flex-start;
            margin-top: 20px;
            color: $color-medium;
        }

        &__actions {
            margin-top: 60px;
        }
    }

    @include media(0, $screen-lg) {
        flex-direction: column;
        align-items: center;
        height: fit-content;
        max-width: 100vw;

        &__image {
            width: calc(100vw - 80px);
            height: calc(100vw - 80px);

            object-fit: cover;
        }

        &__infoContainer {
            margin-bottom: 60px;
            margin-top: 40px;
        }

        &__actions {
            margin-top: 40px;
        }
    }

    @include media(0, $screen-sm) {
        max-width: calc(100vw - 20px);

        &__image {
            width: calc(100vw - 20px);
            height: calc(100vw - 20px);

            object-fit: cover;
        }
    }
}