.mediaList {

    &__element {
        position: relative;
        margin-bottom: 10px;

        cursor: pointer;

        &>img {
            object-fit: cover;
            width: 100%;
            max-height: 60vh;
        }
    }

    &__elementTitle {
        position: absolute;
        top: 20px;
        font-size: $font-size-medium;
        color: $color-primary;
        background-color: $color-dark-lighter;
        padding: 4px 14px;
        font-weight: bold;
    }

    &__display {
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background-color: $color-shadow;
        // pointer-events: none;
        user-select: none;

        z-index: 4;
    }

    &__displayContent {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 90%;
        height: 100%;
        margin: 0 auto;

        &>img {
            width: 100%;
            max-height: 80%;
            object-fit: contain;
        }
    }

    &__displayTitle {
        // position: absolute;
        font-size: $font-size-medium;
        font-weight: bold;
        margin-bottom: 10px;
    }

    &__icon {
        cursor: pointer;
        color: $color-white;
        transition: color 200ms ease-out;

        &:hover {
            color: $color-primary;
        }

        &--close {
            align-self: flex-end;
            font-size: 36px;
            margin-bottom: 10px;
        }
    }
}

.mediaNav {
    display: flex;
    align-items: center;
    margin-top: 10px;

    &__link {
        cursor: pointer;
        width: 16px;
        height: 16px;
        background-color: $color-white;
        transition: background-color 200ms ease-out;

        margin-right: 10px;

        &:last-of-type {
            margin-right: 0;
        }

        &:hover {
            background-color: $color-primary;
        }

        &--selected {
            cursor: default;
            background-color: $color-primary;
        }
    }

    &__arrow {
        cursor: pointer;
        font-size: 50px;
        padding: 10px;
        box-sizing: content-box;
        color: $color-white;
        transition: color 200ms ease-out;

        &:hover {
            color: $color-primary;
        }
    }

    @include media (0, $screen-lg) {
        &__link {
            display: none;
        }
    }
}