.horizontalScroll {
    min-width: 100vw;
    width: fit-content;
    height: 100%;

    &__content {
        display: flex;
        width: fit-content;

        transition: left 200ms ease-out;
    }

    @include media(0, $screen-xl) {
        width: 100vw;
        min-height: 100vh;
        height: fit-content;
        margin: 0 auto;

        &__content {
            flex-direction: column;
        }
    }
}

.scrollTrack {
    position: absolute;
    height: 10px;
    // width: calc(100% - (100vw - 150px) + 50vw);
    background-color: $color-dark-lighter;
    top: calc(50vh - 5px);
    left: calc(100vw - 150px);
    z-index: 0;

    &__fill {
        position: absolute;
        background-color: $color-primary;
        height: 100%;
        left: 0;
        top: 0;
        transition: width 200ms ease-out;
    }

    @include media(0, $screen-xl) {
        // transform-origin: left;
        // transform: rotate(90deg);
        display: none;
        
        width: 10px;
        top: calc(100vh - 150px);
        left: calc(50vw - 5px);
    }
}